import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';

import party from '../img/party.png';
import './Games.css';

const osName = platform();

const Rezult = props => (
	<Panel id={props.id}>
		<div class="block-rezult">
			<h1 class="text-friends sensei">ВЫ ЗНАЕТЕ<br/> СВОИХ ДРУЗЕЙ <br/> НА <span class="percent">80%</span> </h1>
			<button class="share">Поделиться</button>
			<button class="again" onClick={props.go} data-to="home" >Сыграть еще</button>
			<img className="party" src={party} alt="друзья" />
		</div>
	</Panel>
);

Rezult.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Rezult;
